import React, { Component } from 'react';
import './Content.css'
import Workspaces from './Workspaces/Workspaces'
class Content extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    render() {
        return (
            <div className="content">
                <div className="topbar">
                    <div className="typography">Bookmarked Workspaces</div>
                    <div className="blue-button">Create Workspaces</div>
                </div>
                <div className="c1">
                    Your bookmarked workspaces will be displayed here
                </div>
                <div className="c2">
                    All Workspaces
                </div>
                <div className="c3">
                   <Workspaces title="workspace 1"/>
                   <Workspaces title="workspace 2"/>
                   <Workspaces title="workspace 3"/>
                </div>
            </div>

        );
    }
}

export default Content;

